@import "../../scss/utils";

.progressbar-wrapper {
	position: relative;
	background: #e7f0f7;
	transition: ease-in-out 0.3s all;
}

.progressbar {
	display: block;
	position: absolute;
	z-index: 9;
	top: 5rem;
	right: 0;
	bottom: 0;
	left: 0;
	margin: auto;
	max-width: 400px;
	width: 80%;
	height: 30px;

	&::before,
	&::after {
		content: "";
		display: block;
		position: absolute;
		z-index: 99;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		border-radius: 15px;
		background-image: $gradient;
		background-position: 0 0;
		background-repeat: repeat-x;
		animation: movebar 5s linear infinite;
	}

	&::before {
		box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.1);
		-moz-box-shadow: 0px 6px 10px 0px rgba(0, 0, 0, 0.75);
		-webkit-box-shadow: 0px 6px 10px 0px rgba(0, 0, 0, 0.75);
	}

	&::after {
		z-index: 9;
		top: 6px;
		filter: blur(16px);
		opacity: 0.7;
	}
}

.progress-style {
	position: absolute;
	z-index: 999;
	height: 4px;
	width: 90%;
	left: 5%;
	top: 6px;
	opacity: 0.3;

	&::before,
	&::after {
		content: "";
		display: block;
		position: absolute;
		height: 4px;
		top: 0;
		border-radius: 2px;
	}

	&::before {
		background: #fff;
		left: 0;
		right: 10px;
	}

	&::after {
		width: 6px;
		background: #fff;
		right: 0;
	}
}

@-moz-keyframes movebar {
	from {
		background-position: 0 0;
	}

	to {
		background-position: 2500px 0;
	}
}

@-webkit-keyframes movebar {
	from {
		background-position: 0 0;
	}

	to {
		background-position: 2500px 0;
	}
}

@-o-keyframes movebar {
	from {
		background-position: 0 0;
	}

	to {
		background-position: 2500px 0;
	}
}

@keyframes movebar {
	from {
		background-position: 0 0;
	}

	to {
		background-position: 2500px 0;
	}
}
